	module.exports = {"data":{"markdownRemark":{"html":"<h2>CSS3 Media Queries</h2>\n<p>Media Queries allow you to have different styles for different devices/screen sizes. Their introduction in CSS3 has greatly eased the building\nof responsive webpages.</p>\n<p>The best approach when designing a responsive website is to think mobile first; meaning that you create your page starting with the design and content\nof the mobile version. You may think that with some scalable sizes ( %, vw or vh ), your page will adapt perfectly to any device. But it will not. Maybe\nfor some very basic design, but certainly not for more common or complex pages!</p>\n<p>When designing your page for smaller devices, you will focus on the main content. On a bigger screen, you will have to readapt some font-sizes, margins,\npaddings and so on in order to keep your site comfortable and readable, but you will also want/need to add more content, the ones you did not judge\nfundamental, and fill in the space created by the screen size.</p>\n<p>The thought process should be:</p>\n<ol>\n<li>Which content to show?</li>\n<li>How to layout?</li>\n<li>Size?</li>\n</ol>\n<h3>The basic syntax</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token atrule\"><span class=\"token rule\">@media</span> only screen and <span class=\"token punctuation\">(</span><span class=\"token property\">min-width</span><span class=\"token punctuation\">:</span> 768px<span class=\"token punctuation\">)</span></span> <span class=\"token punctuation\">{</span>\n      <span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span><span class=\"token property\">padding</span><span class=\"token punctuation\">:</span> 30px<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>The <code>p</code> tag will have a padding of 30px as soon as the screen reaches min 768px width.</p></p>\n<h3>The AND syntax</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>  <span class=\"token atrule\"><span class=\"token rule\">@media</span> only screen and <span class=\"token punctuation\">(</span><span class=\"token property\">min-height</span><span class=\"token punctuation\">:</span> 768px<span class=\"token punctuation\">)</span> and <span class=\"token punctuation\">(</span><span class=\"token property\">orientation</span><span class=\"token punctuation\">:</span> landscape<span class=\"token punctuation\">)</span></span> <span class=\"token punctuation\">{</span>\n    <span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span><span class=\"token property\">padding</span><span class=\"token punctuation\">:</span> 30px<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>The <code>p</code> tag will have a padding of 30px as soon as the screen reaches min 768px height and its orientation is landscape.</p>\n<h3>The OR syntax</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token atrule\"><span class=\"token rule\">@media</span> only screen and <span class=\"token punctuation\">(</span><span class=\"token property\">min-width</span><span class=\"token punctuation\">:</span> 768px<span class=\"token punctuation\">)</span>, <span class=\"token punctuation\">(</span><span class=\"token property\">min-resolution</span><span class=\"token punctuation\">:</span> 150dpi<span class=\"token punctuation\">)</span></span> <span class=\"token punctuation\">{</span>\n      <span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span><span class=\"token property\">padding</span><span class=\"token punctuation\">:</span> 30px<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>The <code>p</code> tag will have a padding of 30px as soon as the screen reaches min 768px width or its resolution reaches min 150dpi.</p>\n<h3>More Information</h3>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/Media_Queries/Using_media_queries\">MDN - media queries</a></li>\n<li><a href=\"https://www.w3schools.com/cssref/css3_pr_mediaquery.asp\">W3 Schools - @media rule</a></li>\n</ul>","fields":{"slug":"/css/css3-media-queries/"},"frontmatter":{"title":"CSS3 Media Queries"}}},"pathContext":{"slug":"/css/css3-media-queries/"}}

